  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #2d7bce;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.container {
  width: 1200px;
}

.search-form {
  font: inherit;
  max-width: 320px;
  margin: auto;
  padding: 8px;
  display: flex;
}

.name {
  letter-spacing: .03em;
  margin: 0 0 5px;
  font-size: 26px;
  font-weight: 700;
  line-height: 1.17;
}

.search-btn {
  width: 100px;
  height: 31px;
  text-align: center;
  letter-spacing: .06em;
  color: #d7d7cc;
  cursor: pointer;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
  box-shadow: none;
  width: 30px;
  background: #a5a7a8;
  border: none;
  border-radius: 0 5px 5px 0;
  align-items: center;
  margin: 0;
  padding: 3px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.88;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), box-shadow .25s cubic-bezier(.4, 0, .2, 1);
