  width: calc(25% - 20px);
  overflow: hidden;
}

.image {
  height: 208px;
}

.load-more {
  width: 200px;
  height: 50px;
  text-align: center;
  letter-spacing: .06em;
  color: #d7d7cc;
  cursor: pointer;
  background: gray;
  border: #d2b2b2;
  border-radius: 4px;
  align-items: center;
  margin: auto;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.88;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.load-more :hover, .load-more :focus {
  background: #e81829;
  border-radius: 4px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.is-hidden {
  opacity: 0;
